<template>
  <div class="qianggouxuzhi">
    <div>
      <div class="qgxz-nav">
      <li :class="{active:active==0}" @click="handleclick(0)"></li>
      <li :class="{active:active==1}" @click="handleclick(1)"></li>
      <li :class="{active:active==2}" @click="handleclick(2)"></li>
    </div>
    <div class="qgxz-content">
      <div class="hdxq" v-if="hdxqcontent==0">
        <div class="guige">
          <p></p>
          <p></p>
          <p><span>    </span></p>
        </div>
        <div class="wenxintishi">
          <h3 class="wxts-title"></h3>
          <p>:<span></span></p>
          <p></p>
          <p></p>
          <p>: <span> </span></p>
        </div>
        <div class="shangpinjieshao">
          <h3 class="wxts-title"></h3>
        </div>
      </div>
      <div class="qgxz" v-else-if="hdxqcontent==1">
        <p>   </p>
        <p>   </p>
        <p>   </p>
        <p>   QQ</p>
        <p>   </p>
      </div>
      <div class="qglc" v-else>
        <div class="liucheng">
          <i></i>
          <h3></h3>
          <p></p>
        </div>
        <div class="liucheng">
          <i></i>
          <h3></h3>
          <p></p>
        </div>
        <div class="liucheng">
          <i></i>
          <h3></h3>
          <p></p>
        </div>
        <div class="liucheng">
          <i></i>
          <h3></h3>
          <p></p>
        </div>
        <div class="liucheng last">
          <i></i>
          <h3></h3>
          <p></p>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'shiyongxuzhi',
  data () {
    return {
      active: 0,
      hdxqcontent: 0
    }
  },
  props: {
    list: Array
  },
  methods: {
    handleclick (n) {
      this.active = this.hdxqcontent = n
    }
  }
}
</script>
<style lang="stylus" scoped>
.active
  color #ec1f3f
  border-bottom solid .2rem #ec1f3f
.qianggouxuzhi
  width 100%
  margin .5rem 0 3rem 0
  background #fff
  .qgxz-nav
    width 100%
    height 2.5rem
    color #666
    display flex
    flex-direction row
    justify-content space-around
    align-items center
    border-bottom solid .1rem #eee
  .qgxz-nav li
    height 2.5rem
    line-height 2.5rem
    padding 0 .2rem
    display inline-block
    font-size .8rem
  .qgxz-content
    width 96%
    margin 1.2rem auto
    position relative
    .hdxq
      width 100%
      .guige p,.wenxintishi p,.wxts-title
        line-height 1.5rem
        font-size .8rem
      .wxts-title,.wxts-title
        color #ec1f3f
      .shangpinjieshao
        padding-bottom 1rem
    .qgxz
      width 100%
    .qgxz p
      line-height 1rem
      font-size .8rem
      padding-bottom .5rem
    .qglc
      width 100%
      display flex
      flex-direction column
      .liucheng
        width 96%
        height 3.5rem
        margin 0 auto
        display flex
        flex-direction column
        position relative
        border-left solid .1rem #eee
      .liucheng h3
        font-size .9rem
        padding-left 1rem
      .liucheng p
        font-size .8rem
        padding-left 1rem
        color #666
        margin-top .5rem
      .liucheng i
        width .8rem
        height .8rem
        background #666
        border-radius 50%
        position absolute
        left -.45rem
        top .1rem
      .liucheng span
        color #ec1f3f
      .last
        border 0
</style>



